<template>
  <v-row
    justify="center"
    aling="center"
  >
    <v-col cols="12" sm="6" md="4">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="dates"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            :loading="load"
            v-model="dateRangeText"
            label="Date range"
            prepend-icon="mdi-calendar"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
      <v-date-picker 
        v-model="dates"
        :min="min"
        :max="max"
        no-title 
        scrollable 
        range
      >
        <v-spacer></v-spacer>
        <v-btn 
          text
          color="primary" 
          @click="menu = false"
        >
          Cancel
        </v-btn>
        <v-btn 
          text 
          color="primary" 
          @click="$refs.menu.save(dates)"
        >
          OK
        </v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>
  </v-row>
</template>

<script>
  export default {
    data: () => ({
      menu: false,
      min: '2019-01-01',
      max: new Date().toISOString().substr(0, 10),
      dates: [],
      load: false
    }),
    computed: {
      dateRangeText () {
        return this.dates.join(' ~ ');
      },
    },
    watch: {
      dates(value) {
        this.load = this.dates ? true : false;
      }
    }
  }
</script>
